.mainPageSegment{
    width : 100%;
    margin : 20px auto !important;
    max-width: 750px;
}
.mainPageImage{
    width: 100% !important;
    height: 200px !important;
    background-size: cover;
    background-image: url("https://i1fxx.github.io/assets/mainPlaceholder1.jpg");
    text-align: center;
    line-height: 200px;
    vertical-align: middle;
    color: white;
    font-size: 4rem;
    font-weight: bold;
    border-radius: .28571429rem;
}
.categoryAppList{
    /*margin-top: 10px !important;*/
    display : block !important;
    overflow: auto !important;
    white-space: nowrap !important;
}
.appsList{
    margin-top : 30px;
}
.allLink{
    display: inline-block;
    float: right;
}
.categoryHeader{
    display: inline-block;
}

.messageBlock{
    margin-top:15px !important;
}
.installButton{
    width : 45%;
}
.appMobileMainImage{
    margin : 0 auto;
    border-radius: .28571429rem;
}
.appPageMobileInnerBlock{
    text-align: center;
}
.secondaryMobilePageButton{
    width : 20%;
}
.buttonMobileBlock{
    margin-top:15px;
}
.appDescMainImage{
    border-radius: .28571429rem;
    width: 100%;
}
.appPageDescInnerBlock{
    display: grid;
}
.installDescButton{
    width: 100%;
    margin-top: 10px !important;
}
.buttonDescBlock{
    width: 100px;
    float: right;
}
.secondaryDescPageButton{
    width: calc(50% - 4px);
}
.descHeaderBlock{
    width: calc(100% - 100px);
    display: inline-block;
}
.appPageSegment{
    width : 100%;
    margin : 20px auto !important;
    padding : 20px !important;
}
.appPageCategory{
    color : rgba(0,0,0,.4) !important;
    font-size : 1em !important;
    margin : -12.5px 0 15px !important;
}
.descriptionBlock{
    margin-top: 15px;
}
.mainCategoryBlock{
    max-width: 750px;
    margin : 20px auto !important;
}
body{
    padding : 15px !important;
    background-color: #f2f2f2 !important	;
}
.ui.card>.image>img{
    max-height: 150px !important;
    object-fit: contain !important;
    min-height: 150px !important;
}
.ui.horizontal.list>.item{
    margin-left: 0px !important;
    margin-right: 1rem;
    margin-top:10px;
}
.ui.horizontal.list{
    text-align: center;
}
